/* axios */
import store from "@/store";
import axios from "axios";
import { Toast } from "vant";

// axios
const request = axios.create({
  baseURL: "http://cba.itlike.com/public/index.php?s=/api/",
  timeout: 5000,
});

request.defaults.headers.post["Content-Type"] = "application/json";

// 
request.interceptors.request.use(
  function (config) {
    // 
    Toast.loading({
      message: "...",
      forbidClick: true,
      loadingType: "spinner",
      duration: 0,
    });
    const token = store.getters.token;
    if (token) {
      config.headers["Access-Token"] = token;
      config.headers.platform = "H5";
    }
    return config;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

// 
request.interceptors.response.use(
  function (response) {
    const res = response.data;
    if (res.status !== 200) {
      Toast(res.message);
      return Promise.reject(res.message);
    } else {
      //  loading 
      Toast.clear();
    }
    // 
    return res;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

export function doGet(url) {
  return get(url, {}, {}, {});
}

/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 * @param {Object} body []
 */

function get(url, headers, params, body) {
  return request.get(url, {
    headers: headers,
    params: params,
    data: body,
  });
}

export function doPost(url, body) {
  return post(url, {}, {}, JSON.stringify(body));
}

export function doPostJson(url, headers, params, body) {
  return post(url, headers, params, JSON.stringify(body));
}

/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 * @param {Object} body []
 */
function post(url, headers, params, body) {
  headers = Object.assign({}, request.defaults.headers.post, headers);
  return request.post(url, body, {
    headers: headers,
    params: params,
  });
}

export function Bodyheaders() {
  return {
    contenType: {
      key: "Content-Type",
      value: {
        json: "application/json",
        wwwFrom: "application/x-www-form-urlencoded",
        formData: "multipart/form-data",
        raw: "application/octet-stream",
      },
    },
  };
}
export default request;
